import React, { Component } from "react";
import uuid from "uuid";
import { jobs } from "../../../utils/data/jobs";
import { Link } from "react-router-dom";

export default class JobDescription extends Component {
  render() {
    const { id } = this.props.match.params;
    if (!!jobs[id]) {
      const {
        company,
        companyType,
        aboutCompany,
        title,
        type,
        salary,
        aboutJob,
        requirements,
        place,
        other,
        time,
        lang,
        experience = "not mentioned",
        level = "not mentioned"
      } = jobs[id];
      const img =
        "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcTyddlI9kojBz7ew9HwSN_zRmbLEUefhrf2NqTCxuyswrRjZNfs";
      return (
        <div className="job-description avoid-navbar pt-5">
          <div className="container">
            <div className="row">
              <div className="col-12 mb-2">
                <div className="company padding border-r bg-white">
                  <div className="company__info">
                    <div
                      className="responsive_img_container"
                      style={{ width: 50, height: 50 }}
                    >
                      <img className="company__info__image " src={img} />
                    </div>
                    <div className="company__info__data ml-sm-4">
                      <p className="company__info__data__name">{company}</p>
                      <p className="company__info__data__type">{companyType}</p>
                      <p className="company__info__data__time">
                        1m ago
                        <i className="fa fa-clock-o" aria-hidden="true" />
                      </p>
                    </div>
                  </div>
                  <p className="text-center w-100">{title}</p>
                </div>
              </div>
              <div className="col-12 mb-2">
                <div className="desc padding border-r bg-white">
                  <h5>Job Description</h5>
                  <p style={{ color: "gray" }}>{aboutJob}</p>
                </div>
              </div>
              <div className="col-12 col-md-7 mb-2">
                <div className="row">
                  <div className="col-12 mb-2 pr-0">
                    <div className="padding border-r bg-white">
                      <h5 className="">Requirements</h5>
                      <ul>
                        {requirements.map(req => (
                          <li key={uuid()}>{req}</li>
                        ))}
                      </ul>
                    </div>
                  </div>
                  <div className="col-12 mb-2 pr-0">
                    {other && (
                      <div className="padding border-r bg-white">
                        <h5 className=" ">Other details</h5>
                        <p style={{ color: "gray" }}>{other}</p>
                      </div>
                    )}
                  </div>
                </div>
              </div>
              <div className="col-12 col-md-5 mb-2 pl-2">
                <div className="col-12 p-0 mb-2">
                  <div className="p-inline  padding border-r bg-white">
                    <p>Experience: {experience}</p>
                    <p>Level: {level}</p>
                    {lang && <p>Language: {lang}</p>}
                  </div>
                </div>
                <div className="col-12 p-0 mb-2">
                  <div className="p-inline padding border-r bg-white">
                    <p>{salary}</p>
                    <p>{time}</p>
                    <p>{place}</p>
                    <p>{type}</p>
                  </div>
                </div>
                <div className="col-12 p-0">
                  <div className=" padding border-r bg-white">
                    <div className="row">
                      <div className="col-4 text-center">
                        <div>
                          <i
                            className="fa fa-calendar mr-2"
                            aria-hidden="true"
                          />
                        </div>
                        <p
                          className="text-center"
                          style={{ color: "black", display: "inline" }}
                        >
                          shortList
                        </p>
                        <p className="text-center p_color">2</p>
                      </div>
                      <div className="col-4 text-center">
                        <div>
                          <i className="fa fa-phone mr-2" aria-hidden="true" />
                        </div>
                        <p
                          className="text-center "
                          style={{ color: "#28bb28", display: "inline" }}
                        >
                          on contact
                        </p>
                        <p className="text-center p_color">2</p>
                      </div>
                      <div className="col-4 text-center">
                        <div>
                          <i className=" fa fa-times-circle mr-2" />
                        </div>
                        <p
                          className="text-center"
                          style={{ color: "#f72b2b", display: "inline" }}
                        >
                          rejected
                        </p>
                        <p className="text-center p_color">2</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <Link
                to="/under-construction"
                className="mx-auto mb-4 btn btn-info"
              >
                Apply now
              </Link>
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <div className="job-description avoid-navbar pt-5">
          <h1>job cannot be found</h1>
        </div>
      );
    }
  }
}
