import {
  FETCH_USER_START,
  FETCH_USER_SUCCESS,
  FETCH_USER_FAILURE,
  LOGOUT
} from "../types";
const initialState = {
  userData: undefined,
  userDataLoading: undefined,
  errors: {
    fetchUserData: false
  }
};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    case FETCH_USER_START: {
      return { ...state, userDataLoading: true };
    }

    case FETCH_USER_SUCCESS: {
      return { ...state, userDataLoading: false, userData: { ...payload } };
    }
    case FETCH_USER_FAILURE: {
      return {
        ...state,
        userDataLoading: null,
        errors: {
          ...state.errors,
          fetchUserData: true
        }
      };
    }

    case LOGOUT: {
      return {
        userData: undefined,
        userDataLoading: null,
        errors: {
          fetchUserData: false
        }
      };
    }

    default:
      return state;
  }
};
