import React, { Component } from "react";
import { Route, Switch, Link } from "react-router-dom";
import {
  Home,
  Profile,
  CompanyProfile,
  Timeline,
  CompleteRegistration,
  JobDescription,
  NotFound
} from "../components/pages";
import Contact from "../components/pages/Contact";
import Privacy from "../components/pages/Privacy";
import PrivateRoute from "../components/reusable/PrivateRoute";

export default class extends Component {
  render() {
    return (
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/jobs" component={Timeline} />
        <Route exact path="/job/:id" component={JobDescription} />
        <PrivateRoute exact path="/profile" component={Profile} />
        <PrivateRoute exact path="/contact" component={Contact} />
        <PrivateRoute exact path="/privacy" component={Privacy} />
        <PrivateRoute exact path="/company" component={CompanyProfile} />
        <Route
          exact
          path="/complete-registration"
          component={CompleteRegistration}
        />
        <Route
          exact
          path="/under-construction"
          render={() => (
            <div
              style={{ height: "100vh" }}
              className="w-100 avoid-navbar mt-4 d-flex justify-content-center align-items-center flex-column"
            >
              <h1>Page under construction</h1>
              <span>
                if you're not registered please register to be able to apply for
                jobs
              </span>
              <Link to="/"> move to home page</Link>
            </div>
          )}
        />
        <Route component={NotFound} />
      </Switch>
    );
  }
}
