import React, { Component } from "react";
import validateAuth from "../../../../utils/validation/validateAuth";

class LoginModal extends Component {
  //lifecycle methods
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      errors: {}
    };
  }

  componentDidMount = () => {};

  //class methods
  onChangeHandler = ({ target }) =>
    this.setState({ [target.name]: target.value });

  __login = () => {
    const { email, password } = this.state;
    const { isValid, errors } = validateAuth("", email, password, "");
    if (!isValid) {
      this.setState({
        ...this.state,
        errors
      });
      return;
    }
    //clearing errors
    this.setState({ email: "", password: "", errors: {} });

    //sending request
    this.props._login(email, password);
  };
  handleKeyDown = e => {
    if (e.keyCode === 13) {
      //enter is pressed
      this.__login();
    }
  };
  render() {
    const { email, password, errors } = this.state;
    return (
      <div
        className="modal fade auth-modal "
        id="loginModal"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="loginModal"
        aria-hidden="true"
        onKeyDown={this.handleKeyDown}
      >
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div
              className="modal-header"
              style={{ backgroundColor: "#00326f", justifyContent: "center" }}
            >
              <img src="/images/Logo.svg" width={40} alt="" />
            </div>
            <div className="modal-body">
              <div className="modal-body__applicant">
                <div className="validation-input-wrapper">
                  <input
                    type="email"
                    name="email"
                    onChange={this.onChangeHandler}
                    placeholder="email address"
                    value={email}
                    className={`${errors.email && "not-valid"}`}
                  />
                  {errors.email && (
                    <span className="text-danger"> {errors.email}</span>
                  )}
                </div>
                <div className="validation-input-wrapper">
                  <input
                    type="password"
                    name="password"
                    onChange={this.onChangeHandler}
                    placeholder="password"
                    value={password}
                    className={`${errors.password && "not-valid"}`}
                  />
                  {errors.password && (
                    <span className="text-danger">{errors.password}</span>
                  )}
                </div>
              </div>
            </div>
            <div className="modal-footer">
              <button
                onClick={this.__login}
                type="button"
                className="general-btn"
              >
                Login
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default LoginModal;
