import React, { Component } from "react";
import Post from "../timeline/Post";
import InfoCard from "../timeline/InfoCard";
import { Brief } from "../profile/profileComponents";
import { connect } from "react-redux";
import { jobs } from "../../../utils/data/jobs";

class Timeline extends Component {
  render() {
    const { userData: ud, profile: pd } = this.props;
    const name = (ud && ud.name) || "user name";
    const lastActiveDate = (ud && ud.lastActiveDate) || "1 min";
    const profession = (pd && pd.job_title) || "user job";
    const img = (pd && pd.image) || "https://placehold.it/100/ddd";
    return (
      <div className="timeline avoid-navbar ">
        <div className="container">
          <div className="row justify-content-center">
            <div className=" col-12 col-md-9 col-lg-3 order-1 order-lg-1">
              <div className="profile__brief main-card-layout relative-container">
                <div className="d-flex flex-column justify-content-center align-items-center mb-2">
                  <div
                    className="img-preview"
                    style={{
                      width: "8rem",
                      height: "8rem"
                    }}
                  >
                    <img className="rounded-circle" src={img} alt="" />
                  </div>
                  <h6>{name}</h6>
                  <span className="text--secondary">{profession}</span>
                </div>
                <span className="hr-line" />
                <div className="d-flex justify-content-center py-2 w-100">
                  <span className="text-muted mr-3">last active</span>
                  <span className="text-muted">{lastActiveDate}</span>
                </div>
              </div>
            </div>
            <div className="col-lg-6 order-3 order-lg-2">
              {jobs.map((job, index) => (
                <Post
                  index={index}
                  userName={job.company}
                  companyType={job.companyType}
                  jobTitle={job.title}
                  profileImg={
                    "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcTyddlI9kojBz7ew9HwSN_zRmbLEUefhrf2NqTCxuyswrRjZNfs"
                  }
                />
              ))}
            </div>
            <div className="col-lg-3 col-12 order-2 order-lg-3">
              <h4 className="right-widget__title">Today's Analysis</h4>
              <div className="row info-card-row">
                <div className="col-12 col-sm-6 col-lg-12">
                  <InfoCard
                    title="Posts"
                    info={jobs.length}
                    src="/images/writing.svg"
                  />
                </div>
                <div className="col-12 col-sm-6 col-lg-12">
                  <InfoCard
                    title={"Jobs"}
                    info={jobs.length}
                    src="/images/suitcase.svg"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  userData: state.user.userData,
  profile: state.profile.profileData
});
export default connect(mapStateToProps, {})(Timeline);
