import React from "react";
import { Link } from "react-router-dom";

const Post = ({
  userName = "userName",
  profileImg = "https://placehold.it/100/ddd",
  jobTitle = "",
  imageSrc = "",
  videoSrc = "",
  postingTime = "1 min ago",
  companyType = "type",
  index
}) => {
  //type props can be (video | image | text)
  return (
    <div className="post">
      <div className="post__header row">
        <div className="col-12 col-md-9 ">
          <div className="w-100 d-flex">
            <div
              className="responsive_img_container"
              style={{
                width: 50,
                height: 50
              }}
            >
              <img className="post__header__icon" src={profileImg} alt="" />
            </div>
            <div className="post__header__text">
              <h5>{userName}</h5>
              <p>{companyType}</p>
            </div>
          </div>
        </div>

        <span className="col-12 col-md-3">
          {postingTime}
          <img src="images/circular-wall-clock.svg" alt="" />
        </span>
      </div>
      <p>{jobTitle}</p>
      {/* only show if post is video type */}
      {videoSrc && (
        <video
          className=""
          width="100%"
          height="100%"
          controls
          src={videoSrc}
          type="video/mp4"
        />
      )}

      {/* only show if post is image type */}
      {!videoSrc && imageSrc && (
        <div
          className="responsive_img_container"
          style={{
            width: "100%",
            height: "15rem"
          }}
        >
          <img src={imageSrc} />
        </div>
      )}
      <Link to={`/job/${index}`} className="btn btn-success apply-btn">
        <img src="/images/tap.svg" width="20" height="20" />
        <span className="ml-2">show details</span>
      </Link>
    </div>
  );
};

export default Post;
