import React from "react";
import uuid from "uuid";

const Certificate = ({ certifications = [] }) => {
  return (
    <div className="profile__certificate main-card-layout">
      <h6 className="profile__certificate__title text-center">
        Certifications
      </h6>
      {certifications.length > 0
        ? certifications.map(crt => (
            <div
              key={uuid()}
              className=" certificate d-flex flex-column relative-container"
            >
              <span>{crt.name}</span>
              <div className="d-flex">
                <span className="text-muted mr-2">From: {crt.source}</span>
                <span className="mt-2" style={{ fontSize: "1.2rem" }}>
                  {crt.year}
                </span>
              </div>
            </div>
          ))
        : "No certifications added"}
    </div>
  );
};
export default Certificate;
