import React, { Component } from "react";
import { BrowserRouter as Router } from "react-router-dom";
import { connect } from "react-redux";
import { MainPagesRoutes } from "./routes";
import Footer from "./components/reusable/Footer";
import { logout, fetchUserData, fetchProfile } from "./redux/actions";
import { NavbarGuest, NavbarUser } from "./components/reusable";

import Axios from "axios";
import { uris } from "./config";

class App extends Component {
  componentDidMount() {
    // only fetch if user is not in redux store
    if (this.props.isAuth) this.props.fetchUserData();
  }
  componentDidUpdate(prevProps, prevState) {
    if (
      this.props.isAuth &&
      this.props.user.userData &&
      prevProps.user.userData !== this.props.user.userData
    ) {
      this.props.fetchProfile(
        this.props.user.userData.type,
        this.props.user.userData._id
      );
    }
  }

  render() {
    const { isAuth, user, profile: prof, loginName } = this.props;
    const { userData = undefined, userJop = undefined } = user;

    const job_title = prof && prof.job_title;
    const image = prof && prof.image;

    return (
      <div className="App">
        <Router>
          <>
            {isAuth ? (
              <NavbarUser
                name={(userData && userData.name) || loginName || undefined}
                profession={job_title || "Job"}
                img={image || "https://placehold.it/50/ddd"}
                _logout={this.props.logout}
              />
            ) : (
              <NavbarGuest />
            )}
            <div className="main-content">
              <MainPagesRoutes />
            </div>

            <Footer />
          </>
        </Router>
      </div>
    );
  }
}
const mapStateToProps = (state, props) => ({
  isAuth: state.auth.isAuth,
  loginName: state.auth.name,
  user: state.user,
  profile: state.profile.profileData
});

export default connect(mapStateToProps, {
  logout,
  fetchUserData,
  fetchProfile
})(App);
