import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchProfile, editProfile } from "../../../redux/actions";
import Loader1 from "../../reusable/loaders/Loader_1";
import {
  Brief,
  Skills,
  Education,
  Experience,
  QuestionBox,
  LanguagesRating,
  Certificate,
  ExtraInfo,
  Languages
} from "./profileComponents";
import moment from "moment";
import { profile, availableLanguages } from "../../../utils/fixtures";

import uuid from "uuid";
import { Link } from "react-router-dom";

export class Profile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true, //should strt with true
      chosenLang: "en"
    };
  }
  componentDidMount() {
    this.setState({ isLoading: false });
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      this.props.loaders.profileFetching === true &&
      prevProps.loaders.profileFetching !== this.props.loaders.profileFetching
    ) {
      this.setState({ ...this.state, isLoading: false });
    }
    if (this.state.isLoading) {
      const timeout = 3000;
      setTimeout(() => {
        /* we suppose to handle internet connection timeout here */
        this.setState({ ...this.state, isLoading: false });
      }, timeout);
    }
  }

  //methods

  _editProfile = (updateKey, updateVal) => {
    if (updateKey && updateVal) {
      let profile = { ...this.props.profileData };
      //in arrays case
      if (
        typeof profile[updateKey] === "object" &&
        profile[updateKey] instanceof Array
      ) {
        profile[updateKey] = [...profile[updateKey], updateVal];
      } else if (
        //in objects case

        typeof profile[updateKey] === "object" &&
        !profile[updateKey] instanceof Array &&
        profile[updateKey] !== null
      ) {
        profile[updateKey] = { ...profile[updateKey], updateVal };
      } else {
        //on other cases
        profile[updateKey] = updateVal;
      }
      if (this.props.userData) {
        this.props.editProfile(profile, this.props.userData.userType);
      }
    }
  };
  _editProfileField = (fieldKey, fieldIndex, newField, lang = false) => {
    /* edits profile arrays */
    if (lang) {
      let profileData = { ...this.props.profileData };
      profileData[fieldKey][this.state.chosenLang][fieldIndex] = newField;
      if (this.props.userData) {
        this.props.editProfile(profileData, this.props.userData.userType);
      }
    } else {
      let profileData = { ...this.props.profileData };
      profileData[fieldKey][fieldIndex] = newField;
      if (this.props.userData) {
        this.props.editProfile(profileData, this.props.userData.userType);
      }
    }
  };
  _deleteProfileField = (fieldKey, fieldIndex) => {
    /* edits profile arrays */
    let profileData = { ...this.props.profileData };
    profileData[fieldKey].splice(fieldIndex, 1);
    if (this.props.userData) {
      this.props.editProfile(profileData, this.props.userData.userType);
    }
  };

  setChosenLang = ({ currentTarget }) => {
    this.setState({ ...this.state, chosenLang: currentTarget.value });
  };

  render() {
    const { isLoading, chosenLang } = this.state;

    if (isLoading) {
      return <Loader1 />;
    } else {
      const { profileData: pd, userData: ud, loaders } = this.props;

      //profile data
      const image = pd && pd.image;
      const job_title = pd && pd.job_title;
      const skills = pd && pd.skills;
      const experience = pd && pd.experience;
      const education = pd && pd.education;
      const languages = pd && pd.languages;
      const certifications = pd && pd.certifications;
      const basic_info = pd && pd.basic_info;
      const questions = pd && pd.questions[chosenLang];
      //user data
      const name = ud && ud.name;
      const last_logout = ud && ud.last_logout;

      return (
        <div className="profile">
          <div className="container">
            <aside className="profile__left-widget">
              <div className="profile__left-widget__wrapper">
                <Brief
                  img={!image ? "http://placehold.it/100/ddd" : image}
                  profession={job_title || "Job Title"}
                  name={name || "user name"}
                  lastActiveDate={last_logout || moment().format("hh:mm a")}
                  _editProfile={this._editProfile}
                  profileEditing={loaders.profileEditing}
                />

                <Skills
                  skills={skills || null}
                  _editProfile={this._editProfile}
                  profileEditing={loaders.profileEditing}
                  _editProfileField={this._editProfileField}
                  _deleteProfileField={this._deleteProfileField}
                />

                <ExtraInfo
                  basic_info={basic_info}
                  _editProfile={this._editProfile}
                />
              </div>
            </aside>
            <div className="non-fixed-container">
              <main className="profile__main">
                <Languages
                  languages={languages}
                  _editProfile={this._editProfile}
                  _deleteProfileField={this._deleteProfileField}
                />

                <Experience
                  experiences={experience || []}
                  _editProfile={this._editProfile}
                  _deleteProfileField={this._deleteProfileField}
                />

                <Education
                  educations={education || []}
                  _editProfile={this._editProfile}
                  _deleteProfileField={this._deleteProfileField}
                />

                <h6
                  style={{ borderRadius: "0" }}
                  className="main-card-layout text-secondary text-center"
                >
                  Questions Answers
                </h6>
                <select
                  value={this.state.chosenLang}
                  style={{ borderRadius: "0" }}
                  className="flex-grow-1 w-100 mb-4 mr-2 btn border select-language main-card-layout"
                  onChange={this.setChosenLang}
                >
                  {availableLanguages.map(lang => (
                    <option key={uuid()} value={lang.value}>
                      {lang.name}
                    </option>
                  ))}
                </select>
                {questions &&
                  (questions.length > 0 ? (
                    <>
                      {questions.map((q, index) => {
                        return (
                          <QuestionBox
                            key={index.toString()}
                            index={index}
                            _editProfile={this._editProfile}
                            _editProfileField={this._editProfileField}
                            videoLink={q.link}
                            title={q.title}
                          />
                        );
                      })}
                    </>
                  ) : (
                    <p className="text-center">no avail questions</p>
                  ))}
              </main>

              <aside className="profile__right-widget">
                <LanguagesRating
                  languages={languages}
                  _editProfile={this._editProfile}
                  _editProfileField={this._editProfileField}
                />

                <Certificate
                  certifications={certifications || []}
                  _editProfile={this._editProfile}
                />
                {/* <div className="profile__cv-actions">
                  <button className="btn btn-success">download CV</button>
                  <button className="btn btn-secondary">show CV</button>
                </div> */}

                <Link to="/complete-registration" className="btn btn-info">
                  Take Exam
                </Link>
              </aside>
            </div>
          </div>
        </div>
      );
    }
  }
}

const mapStateToProps = state => {
  return {
    profileData: state.profile.profileData,
    userData: state.user.userData,

    loaders: { ...state.profile.loaders }
  };
};

export default connect(mapStateToProps, {
  fetchProfile,

  editProfile
})(Profile);
