import React, { Component } from "react";
import JobCard from "./JobsCard";
import PostCard from "./PostCard";
import JobDetailsCard from "./JobDetailsCard";

export class CompanyProfile extends Component {
  state = {
    img: ["images/suitcase.svg", "images/writing.svg"],
    header: ["Jobs", "Posts"],
    span: ["200", "244,2"]
  };
  render() {
    return (
      <div className="company-profile ">
        <div className="container">
          <div className="company-profile__header">
            <img
              alt=""
              className="header__background"
              src="images/Group_390.svg"
            />
            <div className="header__img">
              <img alt="" src="images/vod.svg" />
            </div>
            <div className="header__text">
              <h4>Vodafone Egypt</h4>
              <p>telecom and communication company</p>
            </div>
          </div>

          <div className="company-about row">
            <div className="company-about__text col-lg-8">
              <h4>About</h4>
              <p>
                ras sit amet nibh libero, in gravida nulla. Nulla vel
                metusscelerisque ante sollicitudin Nulla vel metus scelerisque
                ante sollicitudin Cras sit amet nibh libero, in gravida nulla.
                Nulla vel metus scelerisque ante sollicitudin Nulla vel metus
                scelerisque ante sollicitudin
              </p>
            </div>
            <div className="company-about-social col-lg-4">
              <div className="company-location">
                <h5>Vodafone Egypt</h5>
                <img alt="" src="images/placeholder.svg" />
                <span>Cairo,Egypt</span>
              </div>
              <div className="company-contacts d-flex">
                <div className="company-contacts__icons">
                  <h5>Contacts</h5>
                  <ul className="list-unstyled d-flex">
                    <li>
                      <img alt="" className="" src="images/facebook.svg" />
                    </li>
                    <li>
                      <img alt="" className="" src="images/instagram.svg" />
                    </li>
                    <li>
                      <img alt="" className="" src="images/twitter.svg" />
                    </li>
                    <li>
                      <img alt="" className="" src="images/linkedin.svg" />
                    </li>
                  </ul>
                </div>
                <button className="btn btn-success ml-auto">Chat</button>
              </div>
            </div>
          </div>

          <div className="company-profile-content row">
            <div className="col-lg-2">
              <JobCard
                img={this.state.img[0]}
                header={this.state.header[0]}
                span={this.state.span[0]}
              />
              <JobCard
                img={this.state.img[1]}
                header={this.state.header[1]}
                span={this.state.span[1]}
              />
            </div>
            <div className="col-lg-6">
              <div className="write-post">
                <img alt="" className="vodafone-icon" src="images/vod.svg" />

                <div className="write-post__text">
                  <textarea
                    type="text"
                    placeholder="write your post now....."
                  ></textarea>
                  <div className="d-xl-flex">
                    <div className="upload-video">
                      <img alt="" className="" src="images/youtube.svg" />
                      <span>upload video</span>
                    </div>
                    <div className="apply-btn">
                      <input type="checkbox" />
                      <span>show apply button</span>
                    </div>
                    <div className="job-url">
                      <input type="text" placeholder="job url ...." />
                    </div>
                  </div>
                </div>
              </div>

              <PostCard />
              <PostCard />
            </div>
            <div className="col-lg-4">
              <div className="jobs">
                <h5 className="text-center">Jobs</h5>
              </div>

              <JobDetailsCard />
              <JobDetailsCard />
              <JobDetailsCard />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default CompanyProfile;
